import React from "react";
import {
    Box,
    FooterContainer,
    Row,
    Column,
    FooterLink,
    Heading,
} from "./FooterStyles";
 
const Footer = () => {
    return (
        <Box>
            <FooterContainer>
                <Row>
                    <Column>
                    <a href="/">
                        <Heading>About</Heading>
                        </a>
                    </Column>
                    <Column>
                    <a href="/projects">
                        <Heading>Projects</Heading>
                        </a>
                    </Column>
                    <Column>
                    <a href="/articles">
                        <Heading>Blog</Heading>
                        </a>
                    </Column>
                    <Column>
                    <a href="/contact">
                        <Heading>Contact</Heading>
                        </a>
                                           </Column>
                </Row>
            </FooterContainer>
        </Box>
    );
};
export default Footer;